@import "brandon-grotesque";
@import "reset";

$color: #626262;
$theme-color: #4ccdb6;
$txt-color: #242424;
$bg-color: #fffcf4;

@mixin hor-align {
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
}

@mixin vert-align {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
}

@mixin bg {
  background: {
    repeat: no-repeat;
    position: center;
    size: cover;
  }
}

@mixin hover {
  transition: all 0.3s;
  &:hover {
    transform: scale(1.1);
  }
}

@mixin input-placeholder {
  &.placeholder {
    @content;
  }
  &:-moz-placeholder {
    @content;
  }
  &::-moz-placeholder {
    @content;
  }
  &:-ms-input-placeholder {
    @content;
  }
  &::-webkit-input-placeholder {
    @content;
  }
}

html {
  height: 100%;
}

body {
  height: 100%;
  font-family: "Brandon Grotesque Regular", sans-serif;
}

* {
  box-sizing: border-box;
}

a, a:hover {
  text-decoration: none;
}

.clearfix:after {
  visibility: hidden;
  display: block;
  content: " ";
  clear: both;
  font-size: 0;
  height: 0;
}

.left {
  float: left;
}

.right {
  float: right;
}

.text-center {
  text-align: center;
}

.container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 15px;
}

.main {
  .home {
    @include bg;
    padding-bottom: 70px;
    background-image: url("../images/home-bg.jpg");

    .content {
      position: relative;
      padding: 260px 0;
      text-align: left;
      .fa {
        @include hor-align;
        bottom: 0;
        font-size: 28pt;
        color: $color;
      }
    }

  }

  .home {
    .content {
      display: inline-block;
    }
  }

  .main-title-wrap {
    margin-left: 125px;
  }

}

header {
  padding: 43px 0;
}

.logo {
  font-size: 0;
}

.menu-toggle {
  display: none;
  .fa {
    font-size: 30px;
    color: $color;
    cursor: pointer;
  }
}

.main-nav {
  top: 3px;
  position: relative;
  .menu li {
    display: inline-block;
    margin-right: 35px;
    &:last-child {
      margin-right: 0;
    }
  }
  .nav-link {
    display: inline-block;
    padding-bottom: 7px;
    color: $color;
    font: {
      family: Brandon Grotesque Bold, sans-serif;
      size: 15pt;
    }
    text-transform: uppercase;
    border-bottom: 3px solid transparent;
    transition: all 0.3s;

    &.active, &:hover {
      border-bottom: 3px solid $theme-color;
    }

  }
}

.menu-toggle {
  display: none;
}

.main-title-wrap {
  display: inline-block;
  text-align: center;
}

.main-title {
  margin-bottom: 47px;
  font: {
    family: "Brandon Grotesque Bold", sans-serif;
    size: 54pt;
  }
  color: $theme-color;
}

.main-title-descr {
  font: {
    family: "Brandon Grotesque Light", sans-serif;
    size: 36pt;
  }
  line-height: 1.5;
  color: $color;
}

.text-section {
  padding: 75px 0;
  text-align: center;
  .text-wrapper {
    max-width: 634px;
    margin: 0 auto;
  }
}

.section-title {
  position: relative;
  display: inline-block;
  margin-bottom: 27px;
  padding: 0 60px;
  font: {
    family: "Brandon Grotesque Medium", sans-serif;
    size: 23pt;
  }
  line-height: 1.5;
  color: $color;
  &:before, &:after {
    @include vert-align;
    content: "";
    width: 30px;
    height: 3px;
    background-color: $color;
  }
  &:before {
    left: 0;
  }
  &:after {
    right: 0;
  }
}

.section-title-wrapper {
  position: relative;
  margin-bottom: 20px;
  padding-bottom: 20px;
  &:after {
    @include bg;
    @include hor-align;
    content: "";
    position: absolute;
    width: 27px;
    height: 34px;
    display: inline-block;
    bottom: 0;
    background-image: url(data:image/png;base64,);
  }

}

.section-txt {
  margin: 0 auto 62px;
  font: {
    family: "Brandon Grotesque Thin", sans-serif;
    size: 19pt;
  }
  line-height: 1.5;
  color: $txt-color;
}

.btn {
  position: relative;
  padding: 6px 58px;
  display: inline-block;
  background-color: transparent;
  color: $color;
  font: {
    family: "Brandon Grotesque Medium", sans-serif;
    size: 19pt;
  }
  border: 3px solid;
  outline: none;
  cursor: pointer;
  vertical-align: middle;
  transform: perspective(1px) translateZ(0);
  box-shadow: 0 0 1px transparent;
  transition-property: color;
  transition-duration: 0.3s;
  &:hover, &:focus, &:active{
    color: #fff;
    border-color: $theme-color;
    &:before{
      transform: scaleX(1);
    }
  }
  &:before{
    content: "";
    position: absolute;
    z-index: -1;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: $theme-color;
    transform: scaleX(0);
    transform-origin: 0 50%;
    transition-property: transform;
    transition-duration: 0.3s;
    transition-timing-function: ease-out;
  }
}



.sub-title {
  margin-bottom: 20px;
  font-size: 19pt;
  color: $theme-color;

}

.team-section {
  display: flex;
  align-items: center;
  font-size: 0;

  .team-item {
    width: 50%;
    text-align: center;
    &.text-item {
      padding: 0 55px;
    }

    &.img-item {
      position: relative;
    }

    .delimiter {
      @include vert-align;
      position: absolute;
      left: -40px;
    }

    .text-wrapper {
      max-width: 510px;
      margin: 0 auto;
      .section-txt {
        margin-bottom: 0;
      }
    }

    img {
      width: 100%;
    }

  }
}

.img-section {
  display: flex;
  font-size: 0;

  .left-col {
    width: 42%;
    .img-wrapper:first-child {
      height: 47%;
    }
    .img-wrapper:last-child {
      height: 53%;
    }
  }

  .right-col {
    width: 58%;
    .img-wrapper:first-child {
      height: 64%;
    }
    .image {
      display: inline-block;
      width: 45%;
      height: 36%;
    }
  }

  .img-wrapper {
    position: relative;
    cursor: pointer;
    &:hover .hover-layer {
      opacity: 1;
    }
  }

  img {
    width: 100%;
    height: 100%;
  }

  .text-item {
    width: 55%;
    height: 36%;
    display: inline-block;
    padding: 40px;
    vertical-align: top;
    text-align: center;
    .wrap {
      position: relative;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
    }
    .section-txt {
      margin-bottom: 32px;
    }
  }

}

.hover-layer {
  position: absolute;
  padding: 21px;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  background-color: rgba(76, 205, 182, 0.89);
  opacity: 0;
  z-index: 3;
  transition: all 0.3s;
  .border-wrap {
    height: 100%;
    display: flex;
    padding: 15px;
    justify-content: center;
    align-items: center;
    text-align: center;
    border: 3px solid #fff;
  }
  .text-wrapper {
    max-width: 360px;
  }
  .section-txt {
    margin-bottom: 0;
  }

}

.foot-section {
  @include bg;
  position: relative;
  background-image: url("../images/foot-bg.png");
  z-index: 4;

  .section-icon {
    @include hor-align;
    width: 32px;
    top: 29px;
    background-image: url("../images/section-icon-white.png");
    z-index: 10;
  }
  .content {
    position: relative;
    z-index: 5;
  }
  .section-txt {
    max-width: 400px;
  }

  &:before{
    content: "";
    position: absolute;
    top: 15px;
    bottom: 15px;
    left: 0;
    right: 0;
    border-top: 2px solid #fff;
    border-bottom: 2px solid #fff;
    z-index: 4;
  }

  &:after {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: rgba(76, 205, 182, 0.89);
    z-index: 3;
  }

}

.white-col {
  .section-title-wrapper{
    &:after{
        background-image: url(data:image/png;base64,);
    }
  }
  .section-title {
    color: #fff;
    &:before, &:after {
      background: #fff;
    }
  }

  .section-txt, .btn {
    color: #fff;
  }
}

.footer {
  padding: 35px 0;
  background-color: $color;
  color: #fff;

  .copiright {
    font-size: 20px;
    line-height: 22pt;
  }

}

.social-nav {
  .social-link {
    @include hover;
    display: inline-block;
    margin-right: 35px;
    color: #fff;

    &:last-child {
      margin-right: 0;
    }
    &:hover {
      color: $theme-color;
    }
  }

  .fa {
    font-size: 18pt;
    line-height: 22pt;
  }

}

/*blog*/

.blog {

  .home {
    position: relative;
    padding-bottom: 200px;
    background: {
      color: #e6e6e6;
    }

    .content {
      padding: 91px 0;
      text-align: center;
    }

  }

  .block-section {
    position: relative;
  }

  .text-block {
    position: absolute;
    padding: 70px;
    left: 0;
    right: 0;
    top: -200px;
    .text-wrapper {
      max-width: 580px;
      margin: 0 auto;
    }
  }

  .image-section{
    img{
      width: 100%;
    }
  }

}

.contact .home {
  padding-bottom: 85px;
}

.date {
  margin-bottom: 9px;
  font-size: 14pt;
  color: $color;

}

.text-block {
  max-width: 800px;
  margin: 0 auto;
  text-align: center;
  background: #fff;
  .section-title {
    margin-bottom: 12px;
  }

  .sub-title {
    font-family: "Brandon Grotesque Medium", sans-serif;
  }

  .section-txt {
    margin-bottom: 26px;
  }

}

.image-section img {
  width: 100%;
  height: auto;
}

.blog-section {
  font-size: 0;
}

.blog-item {
  width: 50%;
  display: inline-block;
  overflow: hidden;
  img {
    width: 100%;
    height: auto;
  }
  .text-wrapper {
    padding: 60px 40px;
    background: #fff;
    text-align: center;
    .section-txt {
      font-size: 14pt;
      line-height: 2;
    }
  }
}

.pagination-wrapper {
  padding: 80px 0;
  text-align: center;
}

.pagination {
  a {
    @include bg;
    @include hover;
    display: inline-block;
    margin: 0 25px;
    vertical-align: middle;
    font: {
      family: "Brandon Grotesque Medium", sans-serif;
      size: 17pt;
    }
  }
  .link {
    width: 22px;
    height: 22px;
    background-image: url("../images/link-bg.png");
  }
  .sub-link {
    width: 62px;
    height: 62px;
    background-image: url("../images/sub-link-bg.png");
    line-height: 62px;
    color: $color;
  }
  .active-link {
    width: 82px;
    height: 82px;
    background-image: url("../images/active-link-bg.png");
    line-height: 82px;
    color: $theme-color;
  }
}

/*blog-open*/

