    min-height: 480px;
    position: relative;
    overflow: hidden;
    background: #fff;
    .container {
      position: relative;
      z-index: 2;
    }
    .bg-img {
      position: absolute;
      width: 100%;
      left: 50%;
      bottom: 0;
      transform: translateX(-50%);
      z-index: 1;
    }
    /*@include bg;
    background-position-y: -298px;

    padding-bottom: 0;
    background-image: url("../images/blog-bg.jpg");*/
  }

  .mobile {
    z-index: 2;
  }

  .text-block {
    position: absolute;
    padding: 70px;
    left: 0;
    right: 0;
    top: 410px;
  }

  .text-wrapper {
    margin-bottom: 55px;
    text-align: center;
  }

}

.date span {
  color: $theme-color;
}

.blog-open-txt {
  margin-bottom: 34px;
  font: {
    family: "Adobe Caslon Pro", serif;
    size: 13pt;
  }
  line-height: 27pt;
  text-align: justify;
  color: $color;
}

.blog-open-section {
  background-color: $bg-color;
}

.article-box {
  position: relative;
  max-width: 960px;
  margin: 0 auto;
  padding: 70px 84px;
  top: -70px;
  background-color: #fff;
  z-index: 2;
  &-img {
    width: 100%;
    height: auto;
  }
  .social-nav {
    a:first-child {
      padding-right: 35px;
      border-right: 1px solid $color;
    }
    .social-link {
      color: $color;
    }
    .fa-share-alt {
      color: $theme-color;
    }

  }
  .article-box-img {
