    margin-bottom: 34px;
  }
}

/*modal*/

.custombox-content > * {
  max-height: 100%;
}

.modal-body {
  display: none;
  max-width: 700px;
  margin: 0 20px;
  background: #fff;
  font-size: 0;
  border: 1px solid $color;
  box-shadow: 0 5px 15px rgba(0, 0, 0, .5);
  padding: 45px 35px 25px;
  position: relative;
  .contact-inp {
    width: 100%;
    margin: 0 0 20px 0;
  }

  .demo-close {
    position: absolute;
    right: 9px;
    top: 10px;
  }

  .modal-form {

  }

  .message-inp {
    resize: none;
    max-height: 120px;
  }

  button.contact-btn {
    margin: 0;
  }

  i {
    font-size: 20pt;
    color: $theme-color;
  }

}

/*contact*/

.contact {
  h3, h4 {
    margin: o auto;
    font-family: "Brandon Grotesque Medium", sans-serif;
    text-align: center;
  }

  .contact-title {
    margin-bottom: 20px;
    font-size: 30px;
    color: $theme-color;
  }
  .form-title {
    font-size: 20pt;
    color: $color;
  }

}

.contact-btn{
  color: #fff;
  border-color: #fff;
  &:hover, &:focus, &:active{
    color: $theme-color;
    border-color: #fff;
  }
  &:before {
    background: #fff;
  }
}

.blog.contact {
  .section-title {
    margin-bottom: 50px;
  }
}

.modal-body {
  display: none;
}

.contact-home {
  text-align: center;
}

.contact-form {
  max-width: 540px;
  margin: 0 auto;
  font: {
    family: "Brandon Grotesque Medium", sans-serif;
    size: 24pt;
  }

  .inp {
    width: 100%;
    margin-bottom: 32px;
    padding: 10px 20px;
    font-family: "Brandon Grotesque Medium", sans-serif;
    font-size: 19pt;
    text-align: left;
    color: $color;
    border: 3px solid $color;
    @include input-placeholder {
      font-size: 24pt;
      color: rgba(98, 98, 98, 0.7);
    }

  }

  textarea {
    text-align: justify;
    resize: none;
  }
}

/*mobile-menu*/
.home {
  position: relative;
  overflow: hidden;
}

.mobile {
  width: 200px;
  height: 100%;
  top: 0;
  right: 0;
  z-index: 0;
  position: absolute;
  .menu {
    height: 100%;
    top: 0;
    left: 0;
    margin: 0;
    z-index: 1;
    width: 100%;
    background: $theme-color;
    padding: 89px 0 70px;
    position: absolute;
    transform: translateX(100%);
    transition: transform 0.3s cubic-bezier(1, 0.5, 0, 1);
    &.active {
      transform: translateX(0);
    }
    li {
      display: flex;
      align-items: center;
      height: 20%;
      margin: 0;
      .nav-link {
        width: 100%;
        padding: 25px 15px;
        color: #fff;
        text-align: right;
        transition: background 0.2s ease-in-out;
        border-bottom: 0;
        span {
          border-bottom: 1px dotted #fff;
        }
        &:hover, &:focus, &:active {
          background: rgba(255, 255, 255, 0.2);
          transform: scale(1);
          border-bottom: 0;
        }
      }
    }
  }
}

.menu-toggle {
  position: absolute;
  display: none;
  width: 22px;
  height: 20px;
  padding: 8px 0;
  top: 30px;
  right: 0;
  cursor: pointer;
  z-index: 2;

  span {
    display: block;
    height: 4px;
    background: $color;
    transition: background 0.3s ease-in-out;
  }

  &:before, &:after {
    position: absolute;
    content: "";
    width: 22px;
    height: 4px;
    left: 0;
    background-color: $color;
    transform-origin: center center;
    transition: width 0.2s ease-in-out, background 0.2s ease-in-out, transform 0.2s ease-in-out, top 0.2s ease-in-out, bottom 0.2s ease-in-out, left 0.2s ease-in-out, right 0.2s ease-in-out;
  }
  &:before {
    top: 0;
  }
  &:after {
    bottom: 0;
  }
  &.menu-open {
    span {
      background: rgba(255, 255, 255, 0);
    }
    &:before {
      top: 8px;
      transform: rotate(-45deg);
    }
    &:after {
      bottom: 8px;
      transform: rotate(45deg);
    }
    &:before, &:after {
      width: 26px;
      background: #fff;
    }
    &:hover, &:focus, &:active {
      span {
        background: rgba(255, 255, 255, 0);
      }
      &:before {
        top: 1px;
        left: 8px;
        transform: rotate(45deg);
      }
      &:after {
        left: 8px;
        bottom: 5px;
        transform: rotate(-45deg)
      }
      &:before, &:after {
        width: 18px;
      }
    }
  }
  &:hover, &:focus, &:active {
    span {
      background: rgba(255, 255, 255, 0);
    }
    &::before {
      top: 1px;
      left: -3px;
      transform: rotate(-45deg);
    }
    &::after {
      left: -3px;
      bottom: 5px;
      transform: rotate(45deg)
    }
    &::before, &::after {
      width: 18px;
    }
  }
}

@import "media";


