@media (max-width: 1500px) and (min-width: 769px) {
  .small {

    .text-wrapper {
      max-width: 255px;
    }

    .section-title {
      padding: 0 28px;
      font-size: 19pt;
      &:after, &:before {
        width: 15px;
        height: 2px;
      }
    }

    .section-txt {
      font-size: 14pt;
    }

    .btn {
      padding: 4px 45px;
      font-size: 15pt;
    }

  }
}

@media (max-width: 1100px) and (min-width: 769px) {
  .blog {
    .section-title {
      padding: 0 40px;
      font-size: 21pt;
      &:before, &:after {
        width: 15px;
      }
    }
  }
}

@media (max-width: 1060px) {
  .logo {
    img {
      width: 160px;
    }
  }

  .main-nav {
    .nav-link {
      font-size: 14pt;
    }
  }

  .main .home .content {
    padding: 120px 0;
  }

  .team-section .section-txt {
    font-size: 16pt;
  }

  .img-section {

    .right-col {
      .img-wrapper {
        &:first-child {
          height: 55%;
        }
        &.image {
          display: none;
        }
      }
    }

    .text-item {
      width: 100%;
      height: 45%;
      padding: 20px 35px;
    }

    .section-title {
      padding: 0 31px;
      font-size: 17pt;
      &:before, &:after {
        width: 15px;
      }
    }

  }

  .article-box{
    top: 0;
  }

  .blog-open-section{
    background: #fff;
  }

}

@media (max-width: 960px) {
  .blog {
    .text-block {
      padding: 65px 45px;
      max-width: 650px;
    }

    .section-txt {
      margin-bottom: 34px;
    }

    .section-title {
      margin-bottom: 16px;
      font-size: 19pt;
    }
  }

  .sub-title {
    margin-bottom: 16px;
    font-size: 17pt;
  }

  .date {
    margin-bottom: 12px;
    font-size: 12pt;
  }

  .blog-item .text-wrapper {
    padding: 40px;
  }

  .blog-item {
    width: 100%;
    display: flex;
    align-items: center;
    .text-wrapper {
      padding: 40px 30px;

      .section-txt {
        margin-bottom: 22px;
        line-height: 23pt;
      }
    }

    img {
      height: 100%;
    }
  }

  .contact-form {
    .inp {
      max-height: 300px;
      @include input-placeholder {
        font-size: 19pt;
      }
    }

  }
}
@media (max-width: 878px) {
  .team-section {
    flex-wrap: wrap;
    .team-item {
      width: 100%;

      &.text-item {
        padding: 65px 45px;
        background-color: $bg-color;
      }

      .delimiter {
        top: -137px;
        height: 274px;
        left: 50%;
        transform: rotate(90deg) translateY(41px);
      }
    }

  }

  .blog-item .text-wrapper {
    padding: 30px;
    .btn {
      padding: 4px 45px;
      font-size: 16pt;
    }
  }

  .blog-open .home {
    min-height: 430px;
  }


}

@media (max-width: 769px) {

  header {
    position: relative;
    padding: 30px 0;
    .main-nav {
      display: none;
    }
  }
  .menu-toggle {
    display: inline-block;
  }

  .main {
    .home .content {
      padding: 82px 0 120px;
    }
    .main-title-wrap {
      margin-left: 0;
    }

  }

  .main-title {
    margin-bottom: 17px;
    font-size: 43pt;
  }

  .main-title-descr {
    font-size: 29pt;
  }

  .section-title, .img-section .text-item .section-title {
    padding: 0 40px;
    font-size: 20pt;
    &:before, &:after {
      width: 20px;
      height: 2px;
    }
  }

  /*.section-icon {
    width: 23px;
    height: 30px;
  }*/

  .text-section {
    padding: 65px 45px;
    .text-wrapper {
      max-width: 100%;
    }
  }

  .section-txt {
    margin-bottom: 42px;
    font-size: 16pt;
  }

  .btn {
    padding: 5px 48px;
    font-size: 18pt;
  }

  .img-section {
    flex-wrap: wrap;
    .item.left-col, .right-col {
      width: 100%;
      .img-wrapper {
        width: 50%;
        height: 100%;
        display: inline-block;
        img {
          height: 100%;
        }
      }
    }

    .right-col {
      .img-wrapper {
        display: none;
      }
      .text-item {
        height: 100%;
        padding: 65px 45px;
        background-color: $bg-color;
      }
    }

    .section-title {
      padding: 0 20px;
      font-size: 16pt;
      &:before, &:after {
        width: 11px;
      }
    }

    .section-txt {
      font-size: 16pt;
    }

  }

  .footer .copiright, .social-nav .fa {
    font-size: 15pt;
    line-height: 17pt;
  }

  /*blog*/

  .blog {
    .home {
      padding-bottom: 0;
      .content {
        padding: 90px 0 182px;
      }
    }

    .text-block {
      padding: 65px 45px;
      position: relative;
      top: 0;
    }

    .title-content {
      transition: all 0.3s;
      &.active {
        text-align: left;
      }
    }

    &-item {
      flex-wrap: wrap;
      .img-wrapper{
        height: 400px;
        position: relative;
        width: 100%;
        overflow: hidden;
        &:first-child{
          order: 1;
          img{
            bottom: auto;
          }
        }
      }
      img{
        height: auto;
        position: absolute;
        bottom: 0;
        left: 0;
        right: 0;
      }

      .text-wrapper{
        padding: 65px 45px;
      }
    }

    .image-section, .blog-section{
      &.container{
        padding: 0;
      }
    }
  }

  .contact .home .content {
    padding: 50px 0 152px;
  }

  .blog-open .home {
    min-height: 375px;
  }

  .article-box{
     padding: 60px 35px;
   }

}

@media (max-width: 668px) {

  .blog {
    .main-title-descr {
      font-size: 23pt;
    }
  }

  .main-title {
    font-size: 34pt;
  }

  .main-title-descr {
    font-size: 25pt;
  }

  .logo img {
    width: 150px;
  }

  header {
    padding: 20px 0;
  }

  .menu-toggle {
    top: 23px;
  }

  .mobile .menu {
    padding: 63px 0 70px;
    li {
      .nav-link {
        padding: 10px 15px;
        font-size: 12pt;
      }
    }

  }

  /*blog*/
  .pagination-wrapper{
    padding: 50px 0;
  }

  .pagination {
    a{
      margin: 0 20px;
    }
    .active-link{
      width: 62px;
      height: 62px;
      line-height: 62px;
      font-size: 15pt;
    }
    .sub-link{
      width: 42px;
      height: 42px;
      line-height: 42px;
      font-size: 12pt;
    }
    .link{
      width: 18px;
      height: 18px;
    }



  }

  .blog-open .home {
    min-height: 320px;
  }

}

@media (max-width: 576px) {
  .main .home {
    padding-bottom: 23px;
    .content {
      padding: 60px 0 105px;
    }
  }

  .mobile {
    width: 170px;
    .menu {
      padding-bottom: 23px;
    }
  }

  .main-title {
    margin-bottom: 17px;
    font-size: 33px;
  }

  .main-title-descr {
    font-size: 23px;
  }

  .section-title {
    margin-bottom: 20px;
    padding: 0 20px;
    font-size: 17pt;
    &:before, &:after {
      width: 10px;
    }
  }

  .section-icon {
    margin-bottom: 15px;
  }

  .section-txt {
    margin-bottom: 25px;
  }

  .team-section {
    .team-item {
      &.text-item {
        padding: 45px 45px 65px;
      }

      .delimiter {
        height: 220px;
        top: -110px;
        transform: rotate(90deg) translateY(32px);
        img {
          max-height: 220px;
          width: auto;
        }
      }
    }

  }

  .img-section {
    .item.left-col {
      .img-wrapper:first-child {
        display: none;
      }
      .img-wrapper {
        width: 100%;
      }
    }

  }

  .text-section {
    padding: 45px;
  }

  .footer .copiright, .social-nav {
    display: block;
    float: none;
    text-align: center;
  }
  .footer .copiright {
    margin-bottom: 20px;
  }

  /*blog*/

  .blog .home .content {
    padding: 60px 0 142px;
  }

  .blog .main-title-descr {
    font-size: 16pt;
  }

  .blog .text-block, .blog-item .text-wrapper {
    padding: 45px;
  }

  .blog-open .home {
    min-height: 260px;
  }

}

@media (max-width: 468px) {

  .mobile {
    width: 140px;
    .menu {
      padding-bottom: 15px;
      li {
        .nav-link {
          padding: 8px 15px;
          font-size: 11pt;
        }
      }

    }
  }

  .menu-toggle {
    width: 20px;
    height: 17px;
    padding: 7px 0;

    span {
      height: 3px;
    }

    &:before, &:after {
      width: 20px;
      height: 3px;
    }
    &.menu-open {
      width: 22px;
      height: 19px;
      &:before, &:after {
        width: 24px;
      }
      &:hover, &:focus, &:active {
        &:before, &:after {
          width: 15px;
        }
        &:before {
          top: 2px;
        }
      }
    }
    &:hover, &:focus, &:active {
      &:before, &:after {
        width: 15px;
      }
      &:before {
        top: 0;
      }
    }
  }

  header {
    padding: 20px 0;
  }

  .logo img {
    width: 114px;
  }

  .main .home {
    .content {
      padding: 50px 0 95px;
      .fa {
        font-size: 21pt;
      }
    }
  }

  .main-title {
    margin-bottom: 10px;
    font-size: 30px;
  }

  .main-title-descr {
    font-size: 20px;
  }

  /*blog*/

  .blog .home .content {
    padding: 40px 0 90px;
  }

  .pagination-wrapper {
    padding: 30px 0;
  }

  .pagination{
    a{
      margin: 0 10px;
    }
  }

  .blog-open .home {
    min-height: 230px;
  }

  .contact-form {
    .inp {
      padding: 5px 48px;
      font-size: 18pt;
      @include input-placeholder {
        font-size: 18pt;
      }
    }

  }

  .blog.contact{
    .section-title{
      margin-bottom: 50px;
    }
  }

  .article-box{
    padding: 35px 0px;
  }

}

@media (max-width: 415px) {
  .main .home .content {
    padding: 40px 0 85px;
  }

  .logo img {
    width: 105px;
  }

  .main-title {
    font-size: 25px;
  }

  .main-title-descr {
    font-size: 18px;
  }

  .menu-toggle {
    top: 21px;
  }

  .mobile .menu li .nav-link {
    padding: 8px 15px;
    font-size: 9pt;
  }

  .section-title {
    font-size: 15pt;
  }

  .sub-title {
    font-size: 16pt;
  }

  .text-section, .img-section .right-col .text-item {
    padding: 35px 15px;
  }

  .section-txt, .team-section .section-txt, .img-section .section-txt {
    font-size: 13pt;
  }

  .btn {
    padding: 3px 33px;
    font-size: 16pt;
    border-width: 2px;
  }

  .team-section .team-item.text-item {
    padding: 35px 15px 45px;
  }

  .footer {
    padding: 20px 0;
  }

  .footer .copiright {
    margin-bottom: 16px;
  }

  .blog .main-title-descr {
    max-width: 168px;
  }

  .blog .text-block, .blog-item .text-wrapper {
    padding: 35px 15px;
  }

  .contact-form {
    .inp {
      max-height: 200px;
      padding: 3px 33px;
      font-size: 16pt;
      border-width: 2px;
      @include input-placeholder {
        font-size: 16pt;
      }
    }

  }

  .blog-open .home {
    min-height: 176px;
  }



}

@media (max-width: 350px) {
  .mobile {
    width: 92px;
  }

}

